import brushpng from "../kindpng_723592.png";

interface Brush {
  paint(ctx: CanvasRenderingContext2D, palette: Uint32Array): void;
}

function getRandomColor(palette: Uint32Array) {
  if (palette.length > 0) {
    const num = palette[Math.floor(Math.random() * palette.length)];
    //console.log(num.toString(16));
    let str = num.toString(16);
    // abgr --> rgb
    str = str.substr(6, 2) + str.substr(4, 2) + str.substr(2, 2);
    return "#" + str;
  } else {
    var letters = "0123456789ABCDEF";
    var color = "#";
    for (var i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  }
}

class Pointillist implements Brush {
  paint(ctx: CanvasRenderingContext2D, palette: Uint32Array) {
    ctx.fillStyle = getRandomColor(palette);
    //ctx.globalAlpha = 0.4 + Math.random() * 0.6;

    const x = Math.random() * ctx.canvas.width;
    const y = Math.random() * ctx.canvas.height;
    const rx = Math.random() * ctx.canvas.width * 0.03125;
    const ry = rx;
    ctx.beginPath();
    ctx.ellipse(x, y, rx, ry, 0, 0, 2 * Math.PI);
    ctx.fill();
  }
}

class Round implements Brush {
  paint(ctx: CanvasRenderingContext2D, palette: Uint32Array) {
    ctx.fillStyle = getRandomColor(palette);
    //ctx.globalAlpha = 0.4 + Math.random() * 0.6;

    const x = Math.random() * ctx.canvas.width;
    const y = Math.random() * ctx.canvas.height;
    const rx = Math.random() * ctx.canvas.width * 0.125;
    const ry = Math.random() * ctx.canvas.height * 0.125;
    const rot = Math.random() * Math.PI;
    ctx.beginPath();
    ctx.ellipse(x, y, rx, ry, rot, 0, 2 * Math.PI);
    ctx.fill();
  }
}

class Box implements Brush {
  paint(ctx: CanvasRenderingContext2D, palette: Uint32Array) {
    ctx.fillStyle = getRandomColor(palette);
    const x = Math.random() * ctx.canvas.width;
    const y = Math.random() * ctx.canvas.height;
    const rx = Math.random() * ctx.canvas.width * 0.25;
    const ry = Math.random() * ctx.canvas.height * 0.25;
    ctx.beginPath();
    ctx.fillRect(x, y, rx, ry);
  }
}

class SmallRound implements Brush {
  paint(ctx: CanvasRenderingContext2D, palette: Uint32Array) {
    ctx.fillStyle = getRandomColor(palette);

    const x = Math.random() * ctx.canvas.width;
    const y = Math.random() * ctx.canvas.height;
    const rx = ctx.canvas.width * 0.025;
    const ry = Math.random() * ctx.canvas.height * 0.25;
    const rot = Math.random() * Math.PI;
    ctx.beginPath();
    ctx.ellipse(x, y, rx, ry, rot, 0, 2 * Math.PI);
    ctx.fill();
  }
}

class BoxStrips implements Brush {
  paint(ctx: CanvasRenderingContext2D, palette: Uint32Array) {
    ctx.fillStyle = getRandomColor(palette);

    const x = Math.random() * ctx.canvas.width;
    const y = Math.random() * ctx.canvas.height;
    let rx = 0;
    let ry = 0;
    if (Math.random() > 0.49999999) {
      rx = ctx.canvas.width * 0.025;
      ry = Math.random() * ctx.canvas.height * 0.25;
    } else {
      ry = ctx.canvas.height * 0.025;
      rx = Math.random() * ctx.canvas.width * 0.25;
    }
    ctx.beginPath();
    ctx.fillRect(x, y, rx, ry);
  }
}

class PngBrush implements Brush {
  tmpCanvas: HTMLCanvasElement;
  image: HTMLImageElement;
  loaded: boolean;
  constructor(url) {
    this.loaded = false;
    this.image = new Image();
    this.image.onload = this.onload.bind(this);
    this.image.src = url;
  }
  onload() {
    this.loaded = true;
    this.tmpCanvas = document.createElement("canvas");
    this.tmpCanvas.width = this.image.naturalWidth;
    this.tmpCanvas.height = this.image.naturalHeight;
  }
  paint(ctx: CanvasRenderingContext2D, palette: Uint32Array) {
    if (!this.loaded) {
      return;
    }
    const color = getRandomColor(palette);

    // 1. color my brushstroke and put it in tmpCanvas

    const bctx = this.tmpCanvas.getContext("2d");
    bctx.globalCompositeOperation = "copy";
    bctx.drawImage(this.image, 0, 0);
    // overlay filled rectangle using composition
    bctx.globalCompositeOperation = "multiply";
    //bctx.globalAlpha = 1;
    bctx.fillStyle = color;
    bctx.fillRect(0, 0, this.tmpCanvas.width, this.tmpCanvas.height);
    bctx.globalCompositeOperation = "destination-atop"; // restore transparency
    bctx.drawImage(this.image, 0, 0);

    // ctx.globalCompositeOperation = "copy"; // copy pixel-to-pixel source image
    // ctx.drawImage(image, ...);
    // ctx.globalCompositeOperation = "multiply"; // multiply it by red color
    // ctx.fillStyle = "#FF0000";
    // ctx.fillRect(...);
    // ctx.globalCompositeOperation = "destination-atop"; // restore transparency
    // ctx.drawImage(image, ...);

    // 2. blend the brushstroke onto the ctx using alpha

    //ctx.fillStyle = getRandomColor(palette);
    const x = Math.random() * ctx.canvas.width;
    const y = Math.random() * ctx.canvas.height;
    let rx = 0;
    let ry = 0;
    if (Math.random() > 0.49999999) {
      rx = ctx.canvas.width * 0.025;
      ry = Math.random() * ctx.canvas.height * 0.0833333;
    } else {
      ry = ctx.canvas.height * 0.025;
      rx = Math.random() * ctx.canvas.width * 0.0833333;
    }

    const rot = Math.random() * Math.PI * 2.0;

    ctx.translate(x, y);
    ctx.rotate(rot);
    // center of image
    ctx.drawImage(this.tmpCanvas, 0, 0, rx, ry);
    ctx.rotate(-rot);
    ctx.translate(-x, -y);
  }
}

const brushes: Brush[] = [
  new Round(),
  new Pointillist(),
  new Box(),
  new BoxStrips(),
  new PngBrush(brushpng),
];
export { brushes };
export { Brush };
